.navbar-navbar {
  width: 100%;
  display: flex;
  padding: var(--dl-space-space-doubleunit);
  position: relative;
  max-width: 1000px;
  align-self: center;
  align-items: center;
  padding-left: 2em;
  border-radius: var(--dl-radius-radius-radius4);
  flex-direction: row;
  justify-content: space-between;
  background-color: rgba(183, 181, 140, 0.5);
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
}
.navbar-image {
  width: 84px;
  height: 84px;
  object-fit: cover;
}
.navbar-logo {
  color: var(--dl-color-primary-700);
}
.navbar-right-side {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.navbar-links-container {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.navbar-navlink {
  color: var(--dl-color-primary-100);
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navbar-navlink:hover {
  color: var(--dl-color-primary-700);
  font-weight: 700;
}
.navbar-navlink1 {
  color: var(--dl-color-primary-100);
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navbar-navlink1:hover {
  color: var(--dl-color-primary-700);
  font-weight: 700;
}
.navbar-navlink2 {
  color: var(--dl-color-primary-100);
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navbar-navlink2:hover {
  color: var(--dl-color-primary-700);
  font-weight: 700;
}
.navbar-navlink3 {
  color: var(--dl-color-primary-100);
  transition: 0.3s;
  margin-right: var(--dl-space-space-doubleunit);
  text-decoration: none;
}
.navbar-navlink3:hover {
  color: var(--dl-color-primary-700);
  font-weight: 700;
}
.navbar-navlink4 {
  display: contents;
}
.navbar-component {
  text-decoration: none;
}
.navbar-burger-menu {
  display: none;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}
.navbar-burger-menu {
  width: 24px;
  height: 24px;
}
.navbar-mobile-menu {
  top: 0px;
  flex: 0 0 auto;
  left: 0px;
  width: 100%;
  height: 100%;
  display: none;
  padding: var(--dl-space-space-doubleunit);
  z-index: 100;
  position: absolute;
  flex-direction: column;
  justify-content: space-between;
  background-color: #fff;
}
.navbar-nav {
  flex: 0 0 auto;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.navbar-top {
  flex: 0 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: var(--dl-space-space-doubleunit);
  flex-direction: row;
  justify-content: space-between;
}
.navbar-close-menu {
  flex: 0 0 auto;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.navbar-icon03 {
  width: 24px;
  height: 24px;
}
